// This is your test publishable API key.
const stripe = Stripe(stripe_pk);

let elements;
let createBillingDetails;

$(document).ready(function() {
  if (document.querySelector("#payment-form") != null) {
    if (stripe_payment_elements_enabled === '1') {
      initialize();
      checkStatus();
    } else {
      document.querySelector("#payment-form").style = 'display:none;';
    }

    document
      .querySelector("#payment-form")
      .addEventListener("submit", handleSubmit);

    let $stripeSubmit = document.querySelector('#payment-form #pay-with-payment-option-1');
    let $termsCheck = document.querySelector('.js-conditions-to-approve');
    let $paymentForm = document.querySelector('#payment-form');
    let $paymentOptions = document.querySelector('.payment-options .notice');
    let $placeOrderButton = document.querySelector('.js-payment-confirmation button[type="submit"]');
    let radBtnDefault = document.getElementById("payment-option-1");

    /* By default check the Stripe payment method */
    if (radBtnDefault.dataset.moduleName === 'stripe_official') {
      document.querySelector("#pay-with-payment-option-1-form").style = 'display:block!important;margin-bottom: 15px;';
      radBtnDefault.checked = true;
    }

    $placeOrderButton.addEventListener('click', function (e) {
      e.preventDefault();
      if (stripe_payment_elements_enabled === '1') {
        $stripeSubmit.click();
      } else {
        e.stopPropagation();
        e.stopImmediatePropagation();
        initializeCheckout();
      }
    })

    if (stripe_payment_elements_enabled === '1') {
      $stripeSubmit.style = 'display:none;';
      $paymentForm.style = "display: block!important";
      $paymentOptions.style = "display: none;";
    } else if (stripe_payment_elements_enabled === '0') {
      let $checkoutText = document.getElementById("payment-option-1-additional-information");
      $checkoutText.style = "display: block!important";
    }

    $termsCheck.style = "margin: 15px 0;";

    document.addEventListener('click', function (e) {
      let paymentOptionForm = {};
      if (e.target.name === 'payment-option' && e.target.dataset.moduleName === 'stripe_official') {
        $paymentForm.style = "margin: 15px 0;display: block";
        paymentOptionForm = document.querySelector('#pay-with-' + e.target.id + '-form');
        if (stripe_payment_elements_enabled === '0') {
          paymentOptionForm.style = "display: none!important";
          $paymentForm.style = "display: none!important";
        }
      } else if (e.target.name === 'payment-option' && e.target.dataset.moduleName !== 'stripe_official') {
        $paymentForm.style = "display: none!important";
        paymentOptionForm = document.querySelector('#pay-with-' + e.target.id + '-form');
        paymentOptionForm.style = "display: none!important";
      }
    })
    /* Check the active payment methods */
  }
});

// Fetches a payment intent and captures the client secret
async function initialize() {
//Create elements on the payment page
  const createElements = await fetch(stripe_create_elements, {
    method: "POST",
    headers: {"Content-Type": "application/json"},
    body: JSON.stringify(),
  }).then((r) => r.json());

  createBillingDetails = createElements.billing_details;

  let theme = stripe_theme ? stripe_theme : "stripe";

  const options = {
    mode: createElements.element.mode,
    amount: createElements.element.amount,
    currency: createElements.element.currency.toLowerCase(),
    locale: stripe_locale,
    appearance: {
      theme: theme
    },
    paymentMethodCreation: 'manual'
  };

  elements = stripe.elements(options);

  let elementOptions = {
    defaultValues: {
      billingDetails: {
        address: {
          country: createBillingDetails.billing_details.address.country,
          postal_code: createBillingDetails.billing_details.address.postal_code
        }
      }
    }
  };

  if (stripe_postcode_disabled === 'on') {
    let fields = {
      billingDetails: {
        address: {
          postalCode: 'never'
        }
      }

    };
    elementOptions['fields'] = fields;
  }

  const paymentElement = elements.create("payment", elementOptions);
  paymentElement.mount("#payment-element");
}

async function initializeCheckout() {

  const checkout = await fetch(stripe_create_checkout_url, {
    method: "POST",
    headers: {"Content-Type": "application/json"},
    body: JSON.stringify(),
  }).then((r) => r.json());
  console.log("check: ", checkout);
  window.location = checkout.checkout.url;
}

const handleError = (error) => {
  const messageContainer = document.querySelector('#error-message');
  messageContainer.textContent = error.message;
}

async function handleSubmit(e) {
  e.preventDefault();
//When the customer presses Submit, create the PaymentIntent server-side and confirm client-side
  // Trigger form validation and wallet collection
  const {error: submitError} = await elements.submit();
  if (submitError) {
    handleError(submitError);
    return;
  }

  const pm = await stripe.createPaymentMethod({elements,
    params: createBillingDetails
  });

  if (pm.paymentMethod && pm.paymentMethod.id) {
    const clientSecret = await fetch(stripe_create_intent_url, {
      method: "POST",
      headers: {"Content-Type": "application/json"},
      body: JSON.stringify(pm),
    }).then((r) => r.json());
    let $secret = clientSecret.intent.client_secret;

//If error, redirect to the error page
    stripe.confirmPayment({
      clientSecret: $secret,
      confirmParams: {
        payment_method: pm.paymentMethod.id,
        return_url: stripe_order_confirmation_return_url,
      }
    })
      .then(function (result) {
        if (result.error.type === "card_error" ||
          result.error.type === "invalid_request_error" ||
          result.error.type === "idempotency_error"
        ) {
          window.location = stripe_order_failure_return_url;
        }
        if (result.error.type === "validation_error") {
        }
      });
  }
}

// Fetches the payment intent status after payment submission
async function checkStatus() {
  const clientSecret = new URLSearchParams(window.location.search).get(
    "payment_intent_client_secret"
  );

  if (!clientSecret) {
    return;
  }
}
